import axios from 'axios'
import Fuse from 'fuse.js'

let inputSearch = document.getElementById('search_input')
if(inputSearch) {

  axios.get('/data/site_index.json').then(site => {
    const fuse = new Fuse(site.data, {
      shouldSort: true,
      includeMatches: true,
      threshold: 0.3, // At what point does the match algorithm give up. A threshold of 0.0 requires a perfect match (of both letters and location), a threshold of 1.0 would match anything.
      location: 0, // Determines approximately where in the text is the pattern expected to be found
      distance: 50, // Determines how close the match must be to the fuzzy location (specified by location). An exact letter match which is distance characters away from the fuzzy location would score as a complete mismatch. A distance of 0 requires the match be at the exact location specified. A distance of 1000 would require a perfect match to be within 800 characters of the location to be found using a threshold of 0.8
      maxPatternLength: 12,
      minMatchCharLength: 1,
      keys: ["tt", "tv", "id"]
    })

    inputSearch.addEventListener('keyup', function (e) {
      if (e.key === "Escape") {
        closeSearch()
      }
    })

    inputSearch.addEventListener('input', e => {instantSearch(fuse, inputSearch) })
  })

  let instantSearch = function (fuse, inputSearch) {
    let searchResults = document.getElementById('search_results')
    searchResults.style.display = 'block'
    searchResults.innerHTML = ''
    let results = fuse.search(inputSearch.value)
    document.getElementById('search_input').classList.add('focus');

    results.forEach((resultItem) => {
      highlighter(resultItem);
    })

    let results_html = ''
    let results_ordered    = {}
    let template = document.getElementById('search_results_template')

    let total_count = 0
    results.every(function(result, index) {
      if(!results_ordered[result.item.type]) {
        results_ordered[result.item.type] = { 'count': 0, 'html': '' }
      }

      if(results_ordered[result.item.type]['count'] < 38) {
        results_ordered[result.item.type]['count']++;
        total_count++;
        switch (result.item.type) {
          case 'countries':
            results_ordered['countries']['html'] += country_template(result)
            break;
          case 'bibles':
            results_ordered['bibles']['html'] += bible_template(result)
            break;
          case 'languages':
            results_ordered['languages']['html'] += language_template(result)
            break;
          case 'organizations':
            results_ordered['organizations']['html'] += organization_template(result)
            break;
        }

      }

      return (total_count > 125) ? false : true
    })

    let searchResults_template = template.content.cloneNode(true)
    let search_sections = searchResults_template.querySelectorAll("section")

    search_sections.forEach(search_section => {
      if (results_ordered[search_section.dataset.type]) {
        search_section.innerHTML = results_ordered[search_section.dataset.type]['html']
        search_section.parentNode.setAttribute("data-count", results_ordered[search_section.dataset.type]['count'])
      } else {
        search_section.style.display = 'none'
      }
    });

    searchResults.appendChild(searchResults_template)
  }
}


let highlighter = function(resultItem) {
  resultItem.matches.forEach((matchItem) => {
    let text = resultItem.item[matchItem.key]
    let result = []
    let matches = [].concat(matchItem.indices)
    let pair = matches.shift()

    for (let i = 0; i < text.length; i++) {
      let char = text.charAt(i)
      if (pair && i == pair[0]) {
        result.push('<b>')
      }
      result.push(char)
      if (pair && i == pair[1]) {
        result.push('</b>')
        pair = matches.shift()
      }
    }
    resultItem.highlight = result.join('')
    resultItem.highlight_key = matchItem.key

    if (resultItem.children && resultItem.children.length > 0) {
      resultItem.children.forEach((child) => {
        highlighter(child);
      });
    }
  })
}

function country_template(result) {
  let country = result.item
  return `<a href="/${country.type}/${country.id}">
    <svg class="icon">
    <use xmlns:xlink="https://www.w3.org/1999/xlink" xlink:href="/img/flags.svg#${country.id}"></use>
    </svg>
    <span class="title">${ result.highlight_key == 'tt' ? result.highlight : (country.tt ? country.tt : '') }</span>
    </a>`
}

function bible_template(result) {
  let bible = result.item
  return `<a href="/${bible.type}/${bible.id}">
    <span class="title">${ result.highlight_key == 'tt' ? result.highlight : (bible.tt ? bible.tt : '') } <small>[${bible.id}]</small></span>
    </a>`
}

function language_template(result) {
  let language = result.item
  return `<a href="/${language.type}/${language.id}">
    <span class="title">${ result.highlight_key == 'tt' ? result.highlight : (language.tt ? language.tt : '') } <small>[${language.id}]</small></span>
    </a>`
}

function organization_template(result) {
  let organization = result.item
  return `<a href="/${organization.type}/${organization.id}">
    <span class="title">${ result.highlight_key == 'tt' ? result.highlight : (organization.tt ? organization.tt : '') }</span>
    </a>`
}


function closeSearch() {
  document.getElementById('search_results').style.display = 'none'
  document.getElementById('search_input').value = '';
  document.getElementById('search_input').classList.remove('focus');
}
